@import url(https://fonts.googleapis.com/css2?family=Electrolize&family=Rampart+One&family=Share+Tech+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center}.App .App-header{background-color:#3D6228;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:flex-start;font-size:calc(10px + 2vmin);color:white}.App .App-header .editor,.App .App-header .preview{margin-top:3vh;background-color:#D4F9C9;padding:5px;border-radius:0 20px}.App .App-header .editor header,.App .App-header .preview header{display:flex;justify-content:flex-start;color:#000;font-size:1em;font-family:'Electrolize', sans-serif;height:1.1em}.App .App-header .editor #editor,.App .App-header .preview #editor{width:60vw;min-height:30vh;outline:none;border-style:none;border-radius:0 20px;position:relative;top:10px;font-size:1rem;font-family:'Share Tech Mono', monospace;padding:5px}.App .App-header .editor #editor:focus,.App .App-header .preview #editor:focus{border:2px solid #8AC8BC}.App .App-header .preview header{display:flex;justify-content:flex-start;color:#000;font-size:0.8em;font-family:'Electrolize', sans-serif;height:1.5em}.App .App-header .preview #preview{background-color:#F4FFF1;color:#000;width:60vw;min-height:50vh;outline:none;border-style:none;border-radius:0 20px;font-size:1rem;font-family:'Share Tech Mono', monospace;text-align:left;padding:10px;overflow-wrap:break-word}.App .App-header .preview #preview blockquote{border-left:3px solid #5C1779;color:#002377;padding-left:5px;margin-left:25px}.App .App-header .preview #preview code{background:#FFEAEA;padding:3px;font-weight:bold;font-size:0.875rem}.App .App-header .preview #preview pre{display:block;overflow:auto;background:#FFEAEA;padding:5px;line-height:1.2}.App .App-header .preview #preview h1{border-bottom:2px solid #000}.App .App-header .preview #preview h2{border-bottom:1px solid #000}.App .App-header .preview #preview table{border-collapse:collapse}.App .App-header .preview #preview td,.App .App-header .preview #preview th{border:2px solid #000;padding-left:5px;padding-right:5px}.App .App-header .preview #preview img{display:block;width:10vw}.App .App-header .preview #preview a{color:#1DA1F2}.App .App-header #da3ker{color:white;font-size:0.45em;letter-spacing:2px;font-family:'Rampart One', cursive}

